/* stylelint-disable  no-descending-specificity  */

.Switch {
  position: relative;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: var(--font-primary);
  cursor: pointer;
}

.Switch_size_m {
      --switch-height: var(--control-height-m);
      --box-width: 36px;
      --box-height: 20px;
      --switch-font-size: var(--control-text-size-m);
      --offset: var(--space-xs);
    }

.Switch_size_m .Switch-Input::before {
        -webkit-transform: translateX(-16px);
                transform: translateX(-16px);
      }

.Switch_size_l {
      --switch-height: var(--control-height-m);
      --box-width: 44px;
      --box-height: 24px;
      --switch-font-size: var(--control-text-size-l);
      --offset: var(--space-s);
    }

.Switch_size_l .Switch-Input::before {
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
      }

.Switch-Input {
    position: relative;
    display: inline-block;
    width: var(--box-width);
    height: var(--box-height);
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 99rem;
    border: var(--control-border-width) solid var(--background-color);
    -webkit-transition: background-color 0.15s, border-color 0.15s;
    transition: background-color 0.15s, border-color 0.15s;
    -webkit-appearance: none;
    cursor: pointer;
  }

.Switch-Input::before {
      content: '';
      position: absolute;
      top: 1px;
      right: 1px;
      height: calc(var(--box-height) - var(--control-border-width)*4);
      width: calc(var(--box-height) - var(--control-border-width)*4);
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      border-radius: 999rem;
      background-color: var(--content-color);
      -webkit-transition: background-color 0.15s, -webkit-transform 0.15s;
      transition: background-color 0.15s, -webkit-transform 0.15s;
      transition: transform 0.15s, background-color 0.15s;
      transition: transform 0.15s, background-color 0.15s, -webkit-transform 0.15s;
    }

.Switch-Input:focus,
    .Switch-Input:focus:hover {
      -webkit-box-shadow: 0 0 0 2px var(--color-control-bg-focus);
              box-shadow: 0 0 0 2px var(--color-control-bg-focus);
      -webkit-animation: switchFocusAnimation 0.4s ease-in;
              animation: switchFocusAnimation 0.4s ease-in;
      outline: none;
    }

.Switch-Input:focus:active,
    .Switch-Input:focus:active:hover {
      -webkit-animation-name: none !important;
              animation-name: none !important;
    }

.Switch-Input:checked {
      --background-color: var(--color-control-bg-primary);
      --content-color: var(--color-control-typo-primary);

      background-color: var(--background-color);
    }

.Switch-Input:checked::before {
        -webkit-transform: translateX(0);
                transform: translateX(0);
      }

.Switch-Input:not(:checked) {
      --background-color: var(--color-control-bg-border-default);
      --content-color: var(--color-control-bg-border-default);
    }

.Switch-Label {
    margin-left: var(--offset);
    color: var(--color-control-typo-default);
    font-size: var(--switch-font-size);
  }

.Switch:hover .Switch-Input:not(:checked) {
      --background-color: var(--color-control-bg-border-default-hover);
      --content-color: var(--color-control-bg-border-default-hover);
    }

.Switch:hover .Switch-Input:checked {
      --background-color: var(--color-control-bg-primary-hover);
      --content-color: var(--color-control-typo-primary-hover);
    }

.Switch_disabled {
    pointer-events: none;
  }

.Switch_disabled .Switch-Input:not(:checked):disabled {
      --background-color: var(--color-control-bg-border-disable);
      --content-color: var(--color-control-typo-disable);
    }

.Switch_disabled .Switch-Input:checked:disabled {
      --background-color: var(--color-control-bg-border-disable);
      --content-color: var(--color-control-typo-disable);

      background-color: var(--background-color);
    }

.Switch_disabled .Switch-Input:disabled ~ .switch__label {
      color: var(--color-control-typo-disable);
    }

@-webkit-keyframes switchFocusAnimation {
  from {
    -webkit-box-shadow: 0 0 0 0 var(--color-control-bg-focus), 0 0 0 0 var(--color-control-bg-active);
            box-shadow: 0 0 0 0 var(--color-control-bg-focus), 0 0 0 0 var(--color-control-bg-active);
  }
  to {
    -webkit-box-shadow: 0 0 0 2px var(--color-control-bg-focus), 0 0 0 6px transparent;
            box-shadow: 0 0 0 2px var(--color-control-bg-focus), 0 0 0 6px transparent;
  }
}

@keyframes switchFocusAnimation {
  from {
    -webkit-box-shadow: 0 0 0 0 var(--color-control-bg-focus), 0 0 0 0 var(--color-control-bg-active);
            box-shadow: 0 0 0 0 var(--color-control-bg-focus), 0 0 0 0 var(--color-control-bg-active);
  }
  to {
    -webkit-box-shadow: 0 0 0 2px var(--color-control-bg-focus), 0 0 0 6px transparent;
            box-shadow: 0 0 0 2px var(--color-control-bg-focus), 0 0 0 6px transparent;
  }
}
