.Avatar {
  overflow: hidden;
}

  .Avatar_size_s {
      width: 24px;
      height: 24px;
    }

  .Avatar_size_m {
      width: 32px;
      height: 32px;
    }

  .Avatar_form_round {
      border-radius: 50%;
    }

  .Avatar_form_brick {
      border-radius: 0;
    }

  .Avatar_form_default {
      border-radius: var(--control-radius);
    }

  .Avatar-Image {
    width: 100%;
    height: 100%;
  }
